@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=wrap);
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body, #root {
  height: 100vh;
  margin: 0;
}

body, input, select, textarea, button {
  font-size: 1.6rem;
  font-family: Montserrat, Helvetica, Arial, sans-serif;
}

.grid-container {
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}

header {
  grid-area: header;
  background-color: rgb(4, 56, 228);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem;
}

header a {
  color: #fff;
  outline: none;
  text-decoration: none;
}

a:hover {
  color: rgb(255, 255, 10)
}

main {
  grid-area: main;
}
footer {
  grid-area: footer;
  background-color: rgb(4, 56, 228);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.main {
  -webkit-flex: 3 1 60rem;
          flex: 3 1 60rem;
}

.sidebar {
  -webkit-flex: 1 1 20rem;
          flex: 1 1 20rem;
}

a {
  text-decoration: none;
}


